@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* border: 1px solid red; */
}


.container{
    width: 100%;
    height: 100vh;
   /*  background-color: #E8E4C9; */
    font-family: "Montserrat", sans-serif;
    
}


.body{
    width: 100%;
    height: 100vh;
    display: flex;
    /* justify-content: center; */
    
}
.right{
    width: 22%;
    height: 100vh;
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
}

.upperRight{
    width: 300px;
    height: 43%;
    border-radius: 20px;
    background: rgba( 0, 172, 255, 0.1 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 20px );
    -webkit-backdrop-filter: blur( 20px );
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.bottomRight{
    width: 300px;
    height: 55%;
    border-radius: 20px;
    background-color: #000;
}

.middle{
    width: 50%;
    height: 100vh;
    display: flex;
    background: url('../image/me.png');
    background-size: cover;
    background-position: center;
    object-fit: cover;
}
.nav{
    max-height: 10%;
    max-width: 50%;
    display: flex;
    justify-content: space-evenly;
    background-color: yellowgreen;
    border-radius: 30px;
    margin: 10px;
    
}
.nav h4{
    text-align: center;
    display: flex;
    align-items: center;
    /* color: rebeccapurple; */
}

.nav ul{
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
   
}
.nav li{
    display: inline-block;
    margin-inline: 10px; ;
}
.nav li a{
    color: #000;
    text-decoration: none;
    position: relative;
}
.nav li a::after{
    content: "";
    position: absolute;
    height: 3px;
    width: 0%;
    left: 0;
    bottom: -2px;
    background-color: #000;
    transition: 0.5s;
}
.nav li a:hover::after{
    width: 100%;
}
